const writeText = () => {
  let copyBtn = document.querySelector('.contacts__copy');

  if (copyBtn !== null) {
    copyBtn.addEventListener('click', () => {
      navigator.clipboard.writeText(copyBtn.parentElement.innerText)
        .then(() => {
          console.log('')
        })
        .catch(error => {
          console.error(`   ${error}`)
        })
      })
  }
}

export { writeText }